<template>
    <div style="display: flex; width: 100%;margin-top: 100px;">
        <u-poptip group-id="gift-tip" placement="bottom-start" width="400" :disabled="disabled" trigger="hover" popper-class="popper-class" inner-class="inner-class" arrow-class="arrow-class" transfer>
            <u-button width="80px" height="26px" :skin="img12" rule="1,3||20,10,20,10">hover</u-button>
            <div class="api" slot="content">
                <table>
                    <thead>
                        <tr>
                            <th>Version</th>
                            <th>Update Time</th>
                            <th>Description</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>0.9.5</td>
                            <td>2016-10-26</td>
                            <td>Add new components <code>Tooltip</code> and <code>u-poptip</code></td>
                        </tr>
                        <tr>
                            <td>0.9.4</td>
                            <td>2016-10-25</td>
                            <td>Add new components <code>Modal</code></td>
                        </tr>
                        <tr>
                            <td>0.9.2</td>
                            <td>2016-09-28</td>
                            <td>Add new components <code>Select</code></td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </u-poptip>
        <u-poptip :offset="-20" placement="top-start" width="400" :disabled="disabled" trigger="click" always popper-class="popper-class" inner-class="inner-class" arrow-class="arrow-class" transfer>
            <u-tooltip placement="bottom-start" content="码率越高画面越清晰" :delay="500" theme="dark">
                <u-button width="80px" height="26px" :skin="img12" rule="1,3||20,10,20,10">Clickww</u-button>
            </u-tooltip>
            <div class="api" slot="content">
                <u-input width="200px" number text-align="center" color="#8d97ac" height="26px"/>
                <table>
                    <thead>
                        <tr>
                            <th>Version</th>
                            <th>Update Time</th>
                            <th>Description</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>0.9.5</td>
                            <td>2016-10-26</td>
                            <td>Add new components <code>Tooltip</code> and <code>u-poptip</code></td>
                        </tr>
                        <tr>
                            <td>0.9.4</td>
                            <td>2016-10-25</td>
                            <td>Add new components <code>Modal</code></td>
                        </tr>
                        <tr>
                            <td>0.9.2</td>
                            <td>2016-09-28</td>
                            <td>Add new components <code>Select</code></td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </u-poptip>
        <u-button width="80px" height="26px" ref="showpos" :skin="img12" rule="1,3||20,10,20,10" @on-click="disabled = !disabled">点击禁用提示</u-button>
        <u-tooltip placement="top" content="码率越高画面越清晰" :delay="500" theme="dark">
            <u-button skin="none" width="50px" height="20px" color="#8d97ac" background-color="#3c4454|#4b5568" slot-class="bottom-line">线路</u-button>
        </u-tooltip>

        <div class="popuptip-layer">
            <u-poptip group-id="gift-tip" placement="top-end" @on-popper-hide="onHide" :manual="poppover"
                @on-mouseenter="handleMouseenter1" @on-mouseleave="handleMouseLeave1"
                :value="isShow" width="324" trigger="hover" popper-class="popper-class"
                inner-class="inner-class" arrow-class="arrow-class">
                <div class="popuptip-layer-api" slot="content">
                    <u-input width="200px" number text-align="center" color="#8d97ac" height="26px"/>
                    <table>
                        <thead>
                            <tr>
                                <th>Version</th>
                                <th>Update Time</th>
                                <th>Description</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>0.9.5</td>
                                <td>2016-10-26</td>
                                <td>Add new components <code>Tooltip</code> and <code>u-poptip</code></td>
                            </tr>
                            <tr>
                                <td>0.9.4</td>
                                <td>2016-10-25</td>
                                <td>Add new components <code>Modal</code></td>
                            </tr>
                            <tr>
                                <td>0.9.2</td>
                                <td>2016-09-28</td>
                                <td>Add new components <code>Select</code></td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </u-poptip>
        </div>

        <u-button-group size="small" shape="circle">
            <u-button skin="none" width="200px" height="20px" color="#8d97ac" @on-click="onClick" background-color="#3c4454|#4b5568" slot-class="bottom-line">位置1</u-button>
            <u-button skin="none" width="60px" height="20px" color="#8d97ac" @on-click="onClick" background-color="#3c4454|#4b5568" slot-class="bottom-line">位置2</u-button>
            <u-button skin="none" width="60px" height="20px" color="#8d97ac" @on-click="onClick" background-color="#3c4454|#4b5568" slot-class="bottom-line">位置3</u-button>
            <u-button skin="none" width="60px" height="20px" color="#8d97ac" @on-click="onClick" background-color="#3c4454|#4b5568" slot-class="bottom-line">位置4</u-button>
            <u-button skin="none" width="60px" height="20px" color="#8d97ac" @on-click="onClick" background-color="#3c4454|#4b5568" slot-class="bottom-line">位置5</u-button>
        </u-button-group>
        <br/>
        <u-button-group size="small" shape="circle">
            <u-clip skin="none" width="200px" height="20px" color="#8d97ac" @on-mouseenter="handleMouseenter" @on-mouseleave="handleMouseLeave" background-color="#3c4454|#4b5568" slot-class="bottom-line">位置11</u-clip>
            <u-clip skin="none" width="60px" height="20px" color="#8d97ac" @on-mouseenter="handleMouseenter" @on-mouseleave="handleMouseLeave" background-color="#3c4454|#4b5568" slot-class="bottom-line">位置12</u-clip>
            <u-clip skin="none" width="60px" height="20px" color="#8d97ac" @on-mouseenter="handleMouseenter" @on-mouseleave="handleMouseLeave" background-color="#3c4454|#4b5568" slot-class="bottom-line">位置13</u-clip>
            <u-clip skin="none" width="60px" height="20px" color="#8d97ac" @on-mouseenter="handleMouseenter" @on-mouseleave="handleMouseLeave" background-color="#3c4454|#4b5568" slot-class="bottom-line">位置14</u-clip>
            <u-clip skin="none" width="60px" height="20px" color="#8d97ac" @on-mouseenter="handleMouseenter" @on-mouseleave="handleMouseLeave" background-color="#3c4454|#4b5568" slot-class="bottom-line">位置15</u-clip>
        </u-button-group>
    </div>

</template>
<script>
    import imgtoa from './img/img_tip_bg.png';
    import img12 from './img/carddating/btn_on.png';
    export default {
        data () {
            return {
                gridImg: imgtoa,
                img12: img12,
                disabled: false,
                isShow: false,
                enterTimer: null,
                poppover: {}
            };
        },
        methods: {
            onClick(event) {
                this.isShow = false;
                this.$nextTick(()=>{
                    this.poppover = { reference: event.target };
                    this.isShow = true;
                });
            },
            onHide() {
            },
            handleMouseenter(event) {
                this.poppover = { reference: event.target };
                if (this.enterTimer) clearTimeout(this.enterTimer);
                this.enterTimer = setTimeout(() => {
                    this.isShow = true;
                }, 100);
            },
            handleMouseLeave() {
                if (this.enterTimer) {
                    clearTimeout(this.enterTimer);
                    this.enterTimer = setTimeout(() => {
                        this.poppover = { reference: null };
                        this.isShow = false;
                    }, 100);
                }
            },
            handleMouseenter1() {
                if (this.enterTimer) clearTimeout(this.enterTimer);
                this.enterTimer = setTimeout(() => {
                    this.isShow = true;
                }, 100);
            },
            handleMouseLeave1() {
                if (this.enterTimer) {
                    clearTimeout(this.enterTimer);
                    this.enterTimer = setTimeout(() => {
                        this.poppover = { reference: null };
                        this.isShow = false;
                    }, 100);
                }
            },
        }
    };
</script>
<style>
    .inner-class {
        background-color: blueviolet;
    }
    .popper-class[x-placement^="bottom"]
    .arrow-class:after {
        content: " ";
        top: 1px;
        margin-left: -7px;
        border-top-width: 0;
        border-bottom-width: 7px;
        border-bottom-color: blueviolet;
    }
    .popper-class[x-placement^="top"]
    .arrow-class:after {
        content: " ";
        bottom: 1px;
        margin-left: -7px;
        border-bottom-width: 0;
        border-top-width: 7px;
        border-top-color: blueviolet;
    }
    .bottom-line{
        border-radius: 10px;
        background-color: #3C4454;
        color: #8d97ac;
    }
    .bottom-line a:hover, .bottom-line:active{
        background: #4B5568;
        color: #8d97ac;
    }
    .popuptip-layer {
        pointer-events: none;
    }
    .popuptip-layer-api {
        pointer-events: all;
    }
</style>




